import util from '@/libs/util.js'
import { AccountLogin } from '@/api/sys/login'
import { header } from '@/menu/header'
import aside from '@/menu/aside'
import index from '@/menu/index/index'

// 
// iadminadmin
// import iadmin from '@/menu/admin/admin'
import provider from '@/menu/provider/provider'
import reviewer from '@/menu/reviewer/reviewer'
import monitor from '@/menu/monitor/monitor'

export default {
  namespaced: true,
  actions: {
    /**
     * @description 
     * @param {Object} param context
     * @param {Object} param vm {Object} vue 
     * @param {Object} param username {String} 
     * @param {Object} param password {String} 
     * @param {Object} param usertype {String} 
     * @param {Object} param route {Object} 
     */
    login ({ dispatch }, {
      vm,
      username,
      password,
      usertype,
      route = {
        name: 'index'
      }
    }) {
      // 
      AccountLogin({
        username,
        password,
        usertype
      })
        .then(async res => {
          //  cookie  uuid  token  cookie
          // 
          // uuid    
          // token   token
          //  token 
          console.log('login res:' + JSON.stringify(res))
          util.cookies.set('uuid', res.uuid)
          util.cookies.set('token', res.token)

          //  -----
          // admin provider reviewer monitor
          //  (header and aside) 

          // 
          // 
          let menuHeader = []
          let menuAside = []
          switch (res.usertype) {
            // 
            case 'admin':
              menuHeader = header
              menuAside = aside
              // menuHeader = [ ...index, ...(iadmin[0].children) ]
              // menuHeader.push(...provider)
              // menuAside = [ ...index, ...(iadmin[0].children) ]
              // menuHeader.push(...(provider[0].children))
              // menuAside.push(...(provider[0].children))
              console.log('admin')
              break
            case 'provider':
              menuHeader = [ ...index, ...(provider[0].children) ]
              // menuHeader.push(...provider)
              menuAside = [ ...index, ...(provider[0].children) ]
              // menuAside.push(...provider)
              // menuHeader.push(...(provider[0].children))
              // menuAside.push(...(provider[0].children))
              console.log('provider')
              break
            case 'reviewer':
              menuHeader = [ ...index, ...(reviewer[0].children) ]
              // menuHeader.push(...provider)
              menuAside = [ ...index, ...(reviewer[0].children) ]
              // menuHeader = header
              // menuHeader.push(...reviewer)
              // menuAside = aside
              // menuAside.push(...reviewer)
              // menuHeader.push(...(reviewer[0].children))
              // menuAside.push(...(reviewer[0].children))
              console.log('reviewer')
              break
            case 'monitor':
              menuHeader = [ ...index, ...(monitor[0].children) ]
              // menuHeader.push(...provider)
              menuAside = [ ...index, ...(monitor[0].children) ]
              // menuHeader = header
              // menuHeader.push(...monitor)
              // menuAside = aside
              // menuAside.push(...monitor)
              // menuHeader.push(...(monitor[0].children))
              // menuAside.push(...(monitor[0].children))
              console.log('monitor')
              break
          }
          console.log('each router:' + JSON.stringify(menuHeader))
          // 
          await dispatch('d2admin/menu/headerSet', { menuHeader }, { root: true })
          // 
          await dispatch('d2admin/menu/asideSet', { menuAside }, { root: true })

          //  vuex 
          await dispatch('d2admin/user/set', {
            name: res.name
          }, { root: true })
          // 
          await dispatch('load')
          //   cookie 
          const path = util.cookies.get('redirect')
          // 
          vm.$router.replace(path ? { path } : route)
          //  cookie 
          util.cookies.remove('redirect')
        })
        .catch(err => {
          console.log('err: ', err)
        })
    },
    /**
     * @description 
     * @param {Object} param context
     * @param {Object} param vm {Object} vue 
     * @param {Object} param confirm {Boolean} 
     */
    logout ({ commit }, { vm, confirm = false }) {
      /**
       * @description 
       */
      function logout () {
        // cookie
        util.cookies.remove('token')
        util.cookies.remove('uuid')
        // 
        vm.$router.push({
          name: 'login'
        })
      }
      // 
      if (confirm) {
        commit('d2admin/gray/set', true, { root: true })
        vm.$confirm('?  ', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
          .then(() => {
            commit('d2admin/gray/set', false, { root: true })
            logout()
          })
          .catch(() => {
            commit('d2admin/gray/set', false, { root: true })
            vm.$message('')
          })
      } else {
        logout()
      }
    },
    /**
     * @description 
     * @param {Object} state vuex state
     */
    load ({ commit, dispatch }) {
      return new Promise(async resolve => {
        // DB -> store 
        await dispatch('d2admin/user/load', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/theme/load', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/transition/load', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/page/openedLoad', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/menu/asideCollapseLoad', null, { root: true })
        // DB -> store 
        await dispatch('d2admin/size/load', null, { root: true })
        // end
        resolve()
      })
    }
  }
}
